import React from "react";
import "./Gallery.css";
import { Grid } from "@mui/material";
import { makeStyles } from "@mui/styles";

const imageList = [
  { type: "image", content: "gallery_pic1.png" },
  { type: "image", content: "gallery_pic2.png" },
  {
    type: "text",
    content:
      "Lorem ipsum dolor sit amet, consec adipiscing elit, sed do eiusmod tempo",
  },

  { type: "image", content: "gallery_pic3.png" },
  { type: "image", content: "gallery_pic4.png" },
  {
    type: "text",
    content:
      "Lorem ipsum dolor sit amet, consec adipiscing elit, sed do eiusmod tempo",
    color: "#ffffff",
  },
  { type: "image", content: "gallery_pic5.png" },
  { type: "image", content: "gallery_pic6.png" },
];

const useStyles = makeStyles((theme) => ({
  galleryTextContainer1: {
    height: "250px",
  },
  galleryTextContainer2: {
    height: "250px",
    backgroundColor: "#05595B",
  },
  galleryContainer: {},
}));

function Gallery() {
  const classes = useStyles();
  return (
    <div>
      <div id="gallery">
        <Grid
          container
          spacing={5}
          lg={11}
          // md={8}
          sm={10}
          justifyContent="center"
          alignItems="center"
        >
          {imageList.map((i) => (
            <Grid
              item
              xs
              container
              direction="row"
              justifyContent="center"
              alignItems="center"
              className={classes.galleryContainer}
            >
              {i.type === "image" ? (
                <img
                  className="gallery_image"
                  src={`/gallery/${i.content}`}
                  alt=""
                />
              ) : (
                <Grid
                  item
                  container
                  justifyContent="center"
                  alignItems="center"
                  className={
                    i.color
                      ? classes.galleryTextContainer2
                      : classes.galleryTextContainer1
                  }
                >
                  <p className="gallery_text">{i.content}</p>
                </Grid>
              )}
            </Grid>
          ))}
        </Grid>
      </div>
      <div className="gallery_bottom">
        <img
          src="/gallery/gallery_vector.svg"
          alt=""
          style={{ width: "100%" }}
        />
      </div>
    </div>
  );
}

export default Gallery;
